<template>
  <div class="player-statistic" v-if="selectedPlayerId">
    <div class="select-player-container">
      <div class="player-lable">Игрок:</div>
      <div class="player-selector">
        <model-list-select
          :list="players"
          v-model="selectedPlayerId"
          option-value="id"
          option-text="name"
          placeholder="выберите игрока"
        ></model-list-select>
      </div>
    </div>
    <md-table
      v-model="statisticWithOtherPlayers"
      md-card
      md-fixed-header
      :md-sort.sync="currentSort"
      :md-sort-order.sync="currentSortOrder"
      :md-sort-fn="customSort"
      class="player-table"
    >
      <md-table-row slot="md-table-row" slot-scope="{ item }" md-selectable="single">
        <md-table-cell md-label="Игрок" md-sort-by="name">{{ item.name }}</md-table-cell>
        <md-table-cell md-label="Игр вместе" md-sort-by="gamesTogether" md-numeric>{{ item.gamesTogether }}</md-table-cell>
        <md-table-cell
          md-label="Вместе красные"
          md-sort-by="bothRed"
          md-numeric
        >{{ item.bothRedWins }}/{{ item.bothRed }}&nbsp;&nbsp;&nbsp;{{ item.bothRedWinPercent }}%</md-table-cell>
        <md-table-cell
          md-label="Вместе чёрные"
          md-sort-by="bothBlack"
          md-numeric
        >{{ item.bothBlackWins }}/{{ item.bothBlack }}&nbsp;&nbsp;&nbsp;{{ item.bothBlackWinPercent }}%</md-table-cell>
        <md-table-cell
          md-label="Разноцвет(игрок красный)"
          md-sort-by="playerRed"
          md-numeric
        >{{ item.playerRedWins }}/{{ item.playerRed }}&nbsp;&nbsp;&nbsp;{{ item.playerRedWinPercent }}%</md-table-cell>
        <md-table-cell
          md-label="Разноцвет(игрок чёрный)"
          md-sort-by="playerBlack"
          md-numeric
        >{{ item.playerBlack }}/{{ item.playerBlack }}&nbsp;&nbsp;&nbsp;{{ item.playerBlackWinPercent }}%</md-table-cell>
      </md-table-row>
    </md-table>
  </div>
</template>

<script>
import { ModelListSelect } from "vue-search-select";

export default {
  name: "PlayerStatistic",
  props: { playerStatisticsForDisplay: Array, players: Array },
  components: {
    ModelListSelect
  },
  data: () => ({
    currentSort: "name",
    currentSortOrder: "asc",
    selectedPlayerId: null,
    selectedPlayerName: "",
    statisticWithOtherPlayers: null
  }),
  mounted() {
    this.selectedPlayerId = 1;
  },
  watch: {
    selectedPlayerId: function(val) {
      this.selectedPlayerName = this.getPlayerName(val);
      this.statisticWithOtherPlayers = this.playerStatisticsForDisplay.find(
        x => x.id === val
      ).withOtherPlayers;
    }
  },
  methods: {
    customSort(value) {
      return value.sort((a, b) => {
        const sortBy = this.currentSort;
        var isDesc = this.currentSortOrder === "desc";
        if (
          ["bothRed", "bothBlack", "playerRed", "playerBlack"].includes(sortBy)
        ) {
          return this.percentSort(a, b, sortBy, isDesc);
        }

        if (isDesc) {
          if (a[sortBy] > b[sortBy]) {
            return -1;
          }
          if (a[sortBy] < b[sortBy]) {
            return 1;
          }
          return 0;
        }

        if (a[sortBy] > b[sortBy]) {
          return 1;
        }
        if (a[sortBy] < b[sortBy]) {
          return -1;
        }
        return 0;
      });
    },
    percentSort(a, b, sortBy, isDesc) {
      var percentKey = sortBy + "WinPercent";
      if (a[percentKey] > b[percentKey]) {
        return isDesc ? -1 : 1;
      }
      if (a[percentKey] < b[percentKey]) {
        return isDesc ? 1 : -1;
      }

      if (a[sortBy] > b[sortBy]) {
        return isDesc ? -1 : 1;
      }
      if (a[sortBy] < b[sortBy]) {
        return isDesc ? 1 : -1;
      }

      return 0;
    },
    getPlayerName(id) {
      return this.players.find(x => x.id === id).name;
    }
  }
};
</script>

