<template>
  <md-table
    v-model="playerStatisticsForDisplay"
    md-card
    md-fixed-header
    :md-sort.sync="currentSort"
    :md-sort-order.sync="currentSortOrder"
    :md-sort-fn="customSort"
    class="role-table"
  >
    <md-table-row slot="md-table-row" slot-scope="{ item }" md-selectable="single">
      <md-table-cell md-label="Игрок" md-sort-by="name">{{ item.name }}</md-table-cell>
      <md-table-cell
        md-label="Лучший шериф"
        md-sort-by="sherif"
        md-numeric
      >{{ item.sherifWins }}/{{ item.sherif }}&nbsp;&nbsp;&nbsp;{{ item.sherifWinPercent }}%</md-table-cell>
      <md-table-cell
        md-label="Лучший мирный"
        md-sort-by="citizen"
        md-numeric
      >{{ item.citizenWins }}/{{ item.citizen }}&nbsp;&nbsp;&nbsp;{{ item.citizenWinPercent }}%</md-table-cell>
      <md-table-cell
        md-label="Лучший дон"
        md-sort-by="don"
        md-numeric
      >{{ item.donWins }}/{{ item.don }}&nbsp;&nbsp;&nbsp;{{ item.donWinPercent }}%</md-table-cell>
      <md-table-cell
        md-label="Лучший маф"
        md-sort-by="maf"
        md-numeric
      >{{ item.mafWins }}/{{ item.maf }}&nbsp;&nbsp;&nbsp;{{ item.mafWinPercent }}%</md-table-cell>
    </md-table-row>
  </md-table>
</template>

<script>
export default {
  name: "RoleStatistic",
  props: { playerStatisticsForDisplay: Array, players: Array },
  data: () => ({
    currentSort: "name",
    currentSortOrder: "asc"
  }),
  methods: {
    customSort(value) {
      return value.sort((a, b) => {
        const sortBy = this.currentSort;
        var isDesc = this.currentSortOrder === "desc";
        if (["sherif", "citizen", "don", "maf"].includes(sortBy)) {
          return this.percentSort(a, b, sortBy, isDesc);
        }

        if (isDesc) {
          if (a[sortBy] > b[sortBy]) {
            return -1;
          }
          if (a[sortBy] < b[sortBy]) {
            return 1;
          }
          return 0;
        }

        if (a[sortBy] > b[sortBy]) {
          return 1;
        }
        if (a[sortBy] < b[sortBy]) {
          return -1;
        }
        return 0;
      });
    },
    percentSort(a, b, sortBy, isDesc) {
      var percentKey = sortBy + "WinPercent";
      if (a[percentKey] > b[percentKey]) {
        return isDesc ? -1 : 1;
      }
      if (a[percentKey] < b[percentKey]) {
        return isDesc ? 1 : -1;
      }

      if (a[sortBy] > b[sortBy]) {
        return isDesc ? -1 : 1;
      }
      if (a[sortBy] < b[sortBy]) {
        return isDesc ? 1 : -1;
      }

      return 0;
    }
  }
};
</script>

